
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

import { mapGetters, mapState, mapMutations } from 'vuex'

export default {
components: {
Group,
Cell,
Swiper,
Toast,
Card,
Divider,
Icon
},
data () {
return {
position: 'middle',
successfulCollectionValue: false,
cancelCollectionValue: false,
center: {lng: 116.404, lat: 39.915},
zoom: 11
}
},
methods: {
successfulCollection (position) {
this.position = position
this.successfulCollectionValue = true
},
cancelCollection (position) {
this.position = position
this.cancelCollectionValue = true
},
...mapMutations([
'toggleCollect'
]),
handler ({BMap, map}) {
console.log(BMap, map)
this.center.lng = this.houseDetails[this.currentHouseId].house.center.lng
this.center.lat = this.houseDetails[this.currentHouseId].house.center.lat
this.zoom = 15
}
},
computed: {
...mapGetters([
'currentHouseId'

]),
...mapState([
'houseList',
'houseDetails'
])
},
created () {
console.log(this.currentHouseId)
console.log(this.houseDetails[this.currentHouseId].house.center)
}
}



