import * as types from './mutation-types'

const mutations = {
  [types.setCurrentHouseId] (state, currentHouseId) {
    state.currentHouseId = currentHouseId
  },
  [types.toggleCollect] (state, collectHouseId) {
    if (state.houseList[collectHouseId].collect === false) {
      state.houseList[collectHouseId].collect = true
      state.houseList[collectHouseId].likedNumber += 1
    } else {
      state.houseList[collectHouseId].collect = false
      state.houseList[collectHouseId].likedNumber -= 1
    }
  }
}

export default mutations



