import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import store from './store'
// import Home from './components/HelloFromVux'
import HouseList from './pages/HouseList/HouseList.vue'
import HouseDetails from './pages/HouseDetails/HouseDetails.vue'
import HouseMap from './pages/HouseMap/HouseMap.vue'
import HouseDetailMap from './pages/HouseDetailMap/HouseDetailMap.vue'
import BaiduMap from 'vue-baidu-map'

Vue.use(VueRouter)

Vue.use(BaiduMap, {
  // ak 是在百度地图开发者平台申请的密钥 详见 http://lbsyun.baidu.com/apiconsole/key */
  ak: '4AOGTQyDKKuHOTI3gKRmZfYo2Ifs7iov'
})

const routes = [{
  path: '/',
  component: HouseList
},
{
  path: '/HouseList',
  component: HouseList
},
{
  path: '/HouseDetails/:id',
  component: HouseDetails,
  children: [
    {
      path: '/HouseDetails/HouseDetailMap',
      component: HouseDetailMap
    }
  ]
},
{
  path: '/HouseMap',
  component: HouseMap
}]

const router = new VueRouter({
  routes,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})

FastClick.attach(document.body)

Vue.config.productionTip = false

// 路由跳转
Vue.prototype.$goRoute = function (index) {
  this.$router.push(index)
}

Vue.prototype.$goBack = function () {
  this.$router.go(-1)
}

/* eslint-disable no-new */
new Vue({
  router,
  render: h => h(App),
  store
}).$mount('#app-box')



