
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapGetters, mapState } from 'vuex'
const list = () => ['周边', '路线']

export default {
components: {
Group,
Cell,
Tab,
TabItem,
Tabbar,
TabbarItem,
Swiper,
SwiperItem,
XButton
},
data () {
return {




msg: 'Hello World!',
center: {lng: 116.404, lat: 39.915},
zoom: 11,
list2: list(),
demo2: '周边',
index: 0,
geolocationHeight: document.body.clientWidth * 0.3067,  /* 115px */
panoramaheight: document.body.clientWidth * 0.4187  /* 157px */
}
},
methods: {
handler ({BMap, map}) {
console.log(BMap, map)
this.center.lng = this.houseDetails[this.currentHouseId].house.center.lng
this.center.lat = this.houseDetails[this.currentHouseId].house.center.lat
this.zoom = 15
}
},
computed: {
...mapGetters([
'currentHouseId'

]),
...mapState([
'houseDetails'
])
}
}



